<template>
  <div class="container" id="login">
    <h3 class="text-center">注册</h3>
    <form role="form" class="form-horizontal">
      <form role="form">
        <div class="form-group">
          <label class="control-label col-xs-3">用户名:</label>
          <div class="col-xs-7">
          <input v-model.lazy="userName" type="text" class="form-control" placeholder="请输入用户名"/>
          <p style="color:red">{{msg1}}</p>
          </div>
        </div>
        <div class="form-group">
          <label class="control-label col-xs-3">密码:</label>
          <div class="col-xs-7">
          <input v-model.lazy="password" type="password" class="form-control" placeholder="请输入密码"/>
          <p style="color:red">{{msg2}}</p>
          </div>
        </div>
        <div class="form-group">
          <label class="control-label col-xs-3">性别:</label>
          <div class="col-xs-2">
            <input type="radio" name="sex" id="sex" value="0" v-model="sex">
            <label class="control-label">男</label>
          </div>
          <div class="col-xs-2">
            <input type="radio" name="sex" id="sex" value="1" v-model="sex" >
            <label class="control-label">女</label>
          </div>
        </div>
        <div class="form-group">
          <label class="control-label col-xs-3">电话号码:</label>
          <div class="col-xs-7">
          <input v-model.lazy="phonenumber" type="text" class="form-control" placeholder="电话号码"/>
          <p style="color:red">{{msg3}}</p>
          </div>
        </div>
      </form>
      <div class="form-group">
        <div class="col-xs-8 col-xs-offset-2">
          <p class="key">
            或者<router-link to="/login" >登录</router-link>
            <button @click="register()" type="button" class="btn btn-danger pull-right">登录</button>
          </p>
        </div>
      </div>
    </form>
  </div>
</template>

<script>
export default {
  data(){
    return{
      userName:"",
      password:"",
      sex:"0",
      phonenumber:"",
      msg1:"",
      msg2:"",
      msg3:"",
    }
  },
  methods:{
    register(){
      this.axios({
        method:"post",
        url:"/prod-api/api/register",
        data:{
          "userName":this.userName,
          "password":this.password,
          "phonenumber":this.phonenumber,
          "sex":this.sex,
        },
      })
      .then((resopnse)=>{
        if(resopnse.status == 200){
          this.msg1 = "";
          this.msg2 = "";
          this.msg3 = "";
          if(resopnse.data.code == 200){
            this.$router.push('/login');
          }
          else{
            if(resopnse.data.msg.includes("，")){
              let errormsg = resopnse.data.msg.split("，");
              if(resopnse.data.msg.includes("用户")){
                this.msg1 = errormsg[1];
              }else if(resopnse.data.msg.includes("密码")){
                this.msg2 = errormsg[1];
              }else if(resopnse.data.msg.includes("手机")){
                this.msg3 = errormsg[1];
              }else{
                this.msg1 = errormsg[1];
              }
            }else{
              
              if(resopnse.data.msg.includes("用户")){
                this.msg1 = resopnse.data.msg;
              }else if(resopnse.data.msg.includes("密码")){
                this.msg2 = resopnse.data.msg;
              }else if(resopnse.data.msg.includes("手机")){
                this.msg3 = resopnse.data.msg;
              }else{
                this.msg1 = resopnse.data.msg;
              }
            }
          }
        }
      })
      .catch((error)=>{
        console.log(error)
      })
    }
  },
  mounted(){
    
  },
  beforeUpdate(){
    
  },
}
</script>

<style>

</style>